/* ========================================================================
 * DOM-based Routing
 * Based on http://goo.gl/EUTi53 by Paul Irish
 *
 * Only fires on body classes that match. If a body class contains a dash,
 * replace the dash with an underscore when adding it to the object below.
 *
 * .noConflict()
 * The routing is enclosed within an anonymous function so that you can
 * always reference jQuery with $, even when in .noConflict() mode.
 * ======================================================================== */

(function($) {

  // Use this variable to set up the common and page specific functions. If you
  // rename this variable, you will also need to rename the namespace below.
  var Sage = {
    // All pages
    'common': {
      init: function() {
        // JavaScript to be fired on all pages
        document.createElement( "picture" );

        window.fwp_is_paging = false;

   $(document).on('eboywp-refresh', function() {
       if (! window.fwp_is_paging) {
           window.fwp_page = 1;
           EWP.extras.per_page = 'default';
       }

       window.fwp_is_paging = false;
   });

   $(document).on('eboywp-loaded', function() {
       window.fwp_total_rows = EWP.settings.pager.total_rows;

       if (! EWP.loaded) {
           window.fwp_default_per_page = EWP.settings.pager.per_page;

           $(window).scroll(function() {
               if ($(window).scrollTop() === $(document).height() - $(window).height()) {
                   var rows_loaded = (window.fwp_page * window.fwp_default_per_page);
                   if (rows_loaded < window.fwp_total_rows) {
                       //console.log(rows_loaded + ' of ' + window.fwp_total_rows + ' rows');
                       window.fwp_page++;
                       window.fwp_is_paging = true;
                       EWP.extras.per_page = (window.fwp_page * window.fwp_default_per_page);
                       EWP.soft_refresh = true;
                       EWP.refresh();
                   }
               }
           });
       }
   });

        var header = $(".navbar-expand-sm");
  $(window).scroll(function() {
      var scroll = $(window).scrollTop();

      if (scroll >= 100) {
          header.removeClass('clearHeader').addClass("bg-white");
      } else {
          header.removeClass("bg-white").addClass('clearHeader');
      }
  });

  var logo = $(".logo_2");
$(window).scroll(function() {
var scroll = $(window).scrollTop();

if (scroll >= 100) {
    logo.removeClass('clearHeader').addClass("hide");
} else {
    logo.removeClass("hide").addClass('clearHeader');
}
});


var logo_2 = $(".hidden");
$(window).scroll(function() {
var scroll = $(window).scrollTop();

if (scroll >= 100) {
  logo_2.removeClass('hidden').addClass("show");
} else {
  logo_2.removeClass("show").addClass('hidden');
}
});

var w_menu = $(".navbar-dark");
$(window).scroll(function() {
var scroll = $(window).scrollTop();

if (scroll >= 100) {
  w_menu.removeClass('navbar-dark').addClass("navbar-light");
} else {
  w_menu.removeClass("navbar-light").addClass('navbar-dark');
}
});


        var module = $('.module, .module-small, .side-image, .jumbotron');
        module.each(function(i) {
            if ($(this).attr('data-background')) {
                $(this).css('background-image', 'url(' + $(this).attr('data-background') + ')');
            }
        });
      },
      finalize: function() {
        // JavaScript to be fired on all pages, after page specific JS is fired

        $(document).on('eboywp-loaded', function() {
     // Scroll to the top of the page after the page is refreshed
     $('input[name="store_name"]:radio').change(function () {
             alert($("input[name='store_name']:checked").val());
             $('#input_2_8').val($("input[name='store_name']:checked").val());
         });
         });



       $('.small-grid').click(function(){
         $('.product').removeClass('w-32 w-49').addClass('w-24');
       });
       $('.medium-grid').click(function(){
         $('.product').removeClass('w-32 w-49').addClass('w-32');
       });
       $('.large-grid').click(function(){
         $('.product').removeClass('w-24 w-32').addClass('w-49');
       });
         // runs isotope reload on eboywp change
         $(document).on('eboywp-loaded', function() {
      //   	$container.isotope('reloadItems').isotope();
         });
      }
    },
    // Home page
    'home': {
      init: function() {
        // JavaScript to be fired on the home page
      },
      finalize: function() {
        // JavaScript to be fired on the home page, after the init JS
      }
    },
    // About us page, note the change from about-us to about_us.
    'about_us': {
      init: function() {
        // JavaScript to be fired on the about us page
      }
    }
  };

  // The routing fires all common scripts, followed by the page specific scripts.
  // Add additional events for more control over timing e.g. a finalize event
  var UTIL = {
    fire: function(func, funcname, args) {
      var fire;
      var namespace = Sage;
      funcname = (funcname === undefined) ? 'init' : funcname;
      fire = func !== '';
      fire = fire && namespace[func];
      fire = fire && typeof namespace[func][funcname] === 'function';

      if (fire) {
        namespace[func][funcname](args);
      }
    },
    loadEvents: function() {
      // Fire common init JS
      UTIL.fire('common');

      // Fire page-specific init JS, and then finalize JS
      $.each(document.body.className.replace(/-/g, '_').split(/\s+/), function(i, classnm) {
        UTIL.fire(classnm);
        UTIL.fire(classnm, 'finalize');
      });

      // Fire common finalize JS
      UTIL.fire('common', 'finalize');
    }
  };

  // Load Events
  $(document).ready(UTIL.loadEvents);

})(jQuery); // Fully reference jQuery after this point.
